<template lang="html">
  <section class="games-menu">
    <el-menu :default-active="gameType" mode="vertical" unique-opened>
      <template v-for="group in menu">
        <el-submenu :index="String(group.id)" :key="group.id">
          <template slot="title">
            <span>{{group.name}}</span>
          </template>

          <template v-for="item in group.children">
            <el-menu-item @click="$emit('on-change', { groupId: group.id, itemId: item.id })" :index="String(item.id)" :key="item.id">
              <span>{{item.name}}</span>
            </el-menu-item>
          </template>
        </el-submenu>
      </template>
    </el-menu>
  </section>
</template>

<script>
export default {
  name: 'gamesMenu',
  props: {
    gameType: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      menu: []
    }
  },
  created () {
    this.getGamesMenu()
  },
  methods: {
    getGamesMenu () {
      this.$httpAPI.fetchGamesMenu().then(response => {
        this.menu = response.data.data
      }).catch(error => console.log(error))
    }
  }
}
</script>
